.productDetail {
  border: solid 2px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  width: 100vw;
  justify-items: center;
  align-items: center;
  color: #202124;
}

.productInfo {
  display: grid;
  align-items: center;
  justify-items: center;
  font-size: 2.2rem;
  grid-column: 1/-1;
  grid-template-columns: 1fr 1fr;
}

.productInfo button {
  background-color: #000000;
  color: white;
  border: none;
  transition: all ease-in-out 0.1s;
  font-family: "Luckiest Guy", cursive;
  padding-top: 5px;
  text-align: center;
  height: 40px;
  min-width: 120px;
  padding-left: 10px;
  padding-right: 10px;
  font-size: 1.8rem;
}

.productInfo button:hover {
  cursor: pointer;
  background-color: white;
  color: #000000;
  outline: solid 3px black;
}

.productDetail img {
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  border-radius: 12px;
  outline: solid 16px #000000;
  width: 20em;
  height: auto;
}

.productText {
  display: grid;
  grid-gap: 5px;
  text-align: center;
}

.amountWrap {
  display: grid;
  grid-template-columns: auto auto auto;
  align-items: center;
  align-content: center;

  text-align: center;
}

.amountWrap p {
  padding-top: 5px;
}

.amountWrap i:hover {
  cursor: pointer;
}

.detailWrap i:hover {
  cursor: pointer;
}

@media only screen and (max-width: 980px) {
  .productInfo {
    grid-template-columns: 1fr;
    grid-gap: 1.5em;
  }

  .productDetail img {
    margin-left: 50px;
    margin-right: 50px;
    width: 15em;
  }
}
